<template>
  <Form class=' form' @submit="formSubmit">
    <div class="form-block form-block-name">
      <Field class="form-input" v-model="name" name="name" type="text" placeholder="Enter your name" :rules="validateRequired"></Field>
      <ErrorMessage class="form-error" name="name"></ErrorMessage>

    </div>
    <div class="form-block form-block-phone">
      <Field class="form-input" v-model="phone" name="phone" type="text" placeholder="Enter your phone" :rules="validateRequired"></Field>
    <ErrorMessage class="form-error"  name="phone"></ErrorMessage>
    </div>
    <MobileButton class=" form-submit" type="submit">Call me back</MobileButton>
  </Form>
</template>

<script>


import {Form, Field, ErrorMessage} from 'vee-validate'
import MobileButton from '../MobileButton/MobileButton.vue';

export default {
  data: () => ({
    name: '',
    phone: ''
  }),
  components: {
    Form,
    Field,
    ErrorMessage,
    MobileButton
  },
  methods: {
    validateRequired(value) {
      if(!value) return 'This field is required'
      return true
    },
    formSubmit(value, actions){
      console.log(value);
      actions.resetForm();
    }
  }
}
</script>

